import router from '@/router'

function orderStatusChangeTips(type) {
	var types = {
		delivery_assign: {
			tip: '确定抢单吗',
			success_status: 2
		},
		direct_transfer_refuse: {
			tip: '确定拒绝转单吗',
			success_status: 3,
			reply: 'refuse'
		},
		direct_transfer_agree: {
			tip: '确定接受转单吗',
			success_status: 2,
			reply: 'agree'
		},
		delivery_instore: {
			tip: '确定已取货吗',
			success_status: 3
		},
		delivery_success: {
			tip: '确认已送达吗',
			success_status: 4
		},
		wx_notice: {
			tip: '确定通知下单人你已到达送餐地址吗',
			success_status: 0
		}
	}
	return types[type];
};

function changeOrderStatus(options) {
	var type = options.type;
	if(type == 'delivery_transfer' || type == 'direct_transfer' || type == 'cancel') {
		router.push(options.vue.util.getUrl({path: '/pages/paotui/op', query: {order_id: options.order.id, type: type, order_status: options.order.status}}));
		return;
	}
	var typeInfo = orderStatusChangeTips(type);
	var success_status = typeInfo.success_status;
	if(type == 'direct_transfer_refuse' || type == 'direct_transfer_agree') {
		type = 'direct_transfer_reply';
	}
	var url = 'delivery/order/errander/status';
	if(type == 'wx_notice') {
		url = 'delivery/order/errander/notice';
	}

	var params = {
		url: url,
		data: {
			id: options.order.id,
			type: type,
			reply: typeInfo.reply || '',
			code: options.code
		},
		confirm: options.code ? '' : typeInfo.tip,
		success: function(message) {
			options.vue.util.$toast(message);
			if(type == 'wx_notice') {
				return;
			}
			if(options.from == 'detail') {
				if(type == 'direct_transfer_reply') {
					options.vue.order.transfer_delivery_status = success_status;
					if(typeInfo.reply == 'agree') {
						options.vue.order.deliveryer_id = options.vue.order.transfer_deliveryer_id;
					}
				} else {
					options.vue.order.delivery_status = success_status;
				}
				options.vue.order = Object.assign({}, options.vue.order);
			} else {
				if(typeInfo.reply == 'agree') {
					options.vue.records.data[options.index].transfer_delivery_status = success_status;
				} else if(typeInfo.reply == 'refuse') {
					options.vue.records.data.splice(options.index, 1);
				} else {
					var types = ['delivery_assign', 'delivery_instore', 'delivery_success'];
					if(types.indexOf(type) != -1) {
						options.vue.records.data.splice(options.index, 1);
					}
				}
			}
			if(options.code) {
				options.vue.code = Object.assign({}, {codeShow: false, value: '', orderIndex: -1});
			}
		},
		fail: function(result) {
			options.vue.util.$toast(result.message);
			if(options.code) {
				options.vue.code = Object.assign({}, {codeShow: false, value: '', orderIndex: -1});
			}
		}
	};
	options.vue.util.jspost(params);
};

export {
	orderStatusChangeTips,
	changeOrderStatus,
}



